<template>
    <div class="field has-addons">
        <div class="control is-expanded">
            <input class="input" type="text" placeholder="Search something..." v-model="searching" @keydown="handleKeyDown">
        </div>

        <div class="control">
            <a class="button is-info" @click="doSearch" :disabled="!searching">Search</a>
        </div>
    </div>
</template>

<script>
    import { Search } from '../../api/Search'

    export default {
        data() {
            return {
                searching: ''
            }
        },
        created() {
            const keyword = this.$store.getters.getSearchKeyword

            if (keyword != null) {
                this.searching = keyword

                this.doSearch()
            }
        },
        methods: {
            handleKeyDown(event) {
                if (event.key === "Enter" && this.searching) {
                    this.doSearch()
                }
            },

            doSearch() {
                this.loading()

                this.$store.dispatch('setSearchKeyword', this.searching)

                Search({
                    apiKey: 'AIzaSyB7nVWeBSxb7HC7U3i5SKe6KqXsu8WbcdY',
                    term: this.searching,
                    items: 10
                }, response => window.eventBus.$emit('searchResult', response))
            },

            loading() {
                window.eventBus.$emit('loading', true)
            }
        }
    }
</script>