<template>
  <div id="app">
    <h1 class="subtitle has-text-centered">
      Music Player
    </h1>
    
    <MediaPlayer v-if="playing" :video="video" :playlist="playlist"/>
    
    <div class="tabs is-centered">
        <ul>
            <li><router-link to="/music">Musics</router-link></li> <!--class="is-active"-->
            <li><router-link to="/playlist">Playlist</router-link></li>
        </ul>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
import io from 'socket.io-client'
import MediaPlayer from './components/MediaPlayer.vue'
import { getVideos, addVideo, updateVideo } from './api/Playlist'

export default {
  name: 'App',
  components: {
      MediaPlayer
  },

  data() {
    return {
      socket: {},
      video: null,
      playing: false,
      playlist: false
    }
  },

  created() {
    const self = this

    self.socket = io("https://music-chatbot-api.herokuapp.com")

    //self.socket.emit("Created", "Hello World!")
    //self.socket.on("Created", (data) => console.log(data))
    
    self.socket.on("play", (data) => {
      this.handlePlaying({
        video: {id: (data.playlist) ? data.id : data.id.videoId, _id: data._id},
        playlist: data.playlist,
        status: data.status
      })
    })

    self.socket.on("next", () => {
      window.eventBus.$emit('playNext')
    })

    self.socket.on("prev", () => {
      window.eventBus.$emit('playPrev')
    })

    self.socket.on("playMedia", () => {
      window.eventBus.$emit('playMedia')
    })

    self.socket.on("pauseMedia", () => {
      window.eventBus.$emit('pauseMedia')
    })

    self.socket.on("addToList", (data) => {
      this.addToList(data)
    })

    self.socket.on("updateList", (data) => {
      this.updateList(data)
    })

    self.socket.on("deleteVideo", (data) => {
      window.eventBus.$emit('deleteVideo', data)
    })
  },

  mounted() {
    window.eventBus.$on('playing', this.handlePlaying);
    window.eventBus.$on('addToList', this.addToList);
    window.eventBus.$on('updateList', this.updateList);
  },

  methods: {
    handlePlaying(data) {
      const self = this

      if (data.status) {
        self.$store.dispatch('setCurrent', data.video._id)
      } else {
        self.$store.dispatch('setCurrent', null)
      }

      self.video = data.video
      self.playlist = data.playlist
      self.playing = data.status
    },

    addToList(video) {
      getVideos(videos => {
          const videoAmount = videos.length
          const prevVideo = (videoAmount > 0) ? videos[videoAmount - 1] : null

          if (videoAmount > 0) {
              addVideo({
                  index: prevVideo.index + 1,
                  prev: (prevVideo != null) ? prevVideo._id : null,
                  videoId: video.id.videoId,
                  next: null
              }, newVideo => {
                if (!this.playing) {
                  this.handlePlaying({
                    video: {id: newVideo.videoId, _id: newVideo._id},
                    playlist: true,
                    status: true
                  })
                }
                
                updateVideo({
                    _id: prevVideo._id,
                    index: prevVideo.index,
                    prev: prevVideo.prev,
                    videoId: prevVideo.videoId,
                    next: newVideo._id
                }, () => {
                  window.eventBus.$emit('fetchAllVideos')
                })
              })
          } else {
              addVideo({
                  index: videoAmount + 1,
                  prev: (prevVideo != null) ? prevVideo._id : null,
                  videoId: video.id.videoId,
                  next: null
              }, newVideo => {
                if (!this.playing) {
                  this.handlePlaying({
                    video: {id: newVideo.videoId, _id: newVideo._id},
                    playlist: true,
                    status: true
                  })
                }

                window.eventBus.$emit('fetchAllVideos')
              })
          }
      })
    },

    updateList(videos) {
        const videoAmount = videos.length
        
        if (videoAmount > 1) {
            videos.forEach((v, index) => {
                updateVideo({
                  _id: v._id,
                  index: index + 1,
                  prev: (index == 0) ? null : videos[index - 1]._id,
                  videoId: v.videoId,
                  next: ((index + 1) < videoAmount) ? videos[index + 1]._id : null
                }, () => {
                  window.eventBus.$emit('fetchAllVideos')
                })
            })
        }
    }
  }
}
</script>

<style>
  #app {
    margin: auto;
    margin-top: 3rem;
    max-width: 700px;
  }
</style>
