<template>
    <div class="YoutubeDash__wrapper">
        <Finder/>

        <div class="container" v-if="!loading">
            <VideoGroup :videos="videos"/>
        </div>

        <div v-else>
            Loading...
        </div>
    </div>
</template>

<script>
    //import Search from './Search'
    import VideoGroup from './VideoGroup'
    import Finder from './Finder'
    
    export default {
        components: {
            VideoGroup,
            Finder
        },

        mounted() {
            window.eventBus.$on('loading', this.handleLoading);
            window.eventBus.$on('searchResult', this.handleYoutubeVideos);
        },

        created() {
            //console.log("Created");
        },

        data() {
            return {
                videos: null,
                loading: false
            }
        },

        methods: {
            handleYoutubeVideos(videos) {
                this.videos = videos
                this.loading = false

                //console.log(this.videos)
            },

            handleLoading(value) {
                this.loading = value
            },
        },
    }
</script>