<template>
    <div class="Playlist__wrapper">
        <table width="100%">
            <tr>
                <td>
                    <a 
                        href="#"
                        v-if="videos.length > 0"
                        class="is-pulled-right mb-3"
                        @click="deleteAll"> Delete All
                    </a>
                </td>
            </tr>

            <tr>
                <td>
                    <draggable
                        v-if="videos.length > 0"
                        :list="videos"
                        class="list-group"
                        ghost-class="ghost"
                        @end="dropItem"
                    >
                        <div
                            class="list-group-item"
                            v-for="video in videos"
                            :key="video.id"
                        >
                            <PlaylistVideo :video="video"/>
                        </div>
                    </draggable>

                    <div v-else>
                        Add some videos.
                    </div>
                </td>
            </tr>
        </table>
    </div>
</template>

<script>
    import draggable from "vuedraggable";
    import { getVideoById, getVideos, updateVideo, deleteVideo, deleteAllVideos } from '../api/Playlist'
    import { SearchById } from '../api/Search'
    import PlaylistVideo from './Playlist-Video.vue'

    export default {
        components: {
            PlaylistVideo,
            draggable
        },
        data() {
            return {
                videos: []
            }
        },
        mounted() {
            const self = this

            window.eventBus.$on('playPrev', self.handlePlayPrev);
            window.eventBus.$on('playNext', self.handlePlayNext);
            window.eventBus.$on('fetchAllVideos', self.fetchAllVideos);
            window.eventBus.$on('deleteVideo', self.deleteVideo);
        },
        created() {
            this.fetchAllVideos()
        },
        methods: {
            deleteVideo(video) {
                getVideoById(video._id, delVideo => {
                    let prevVideo = null
                    let nextVideo = null

                    const currentId = this.$store.getters.getCurrent

                    if (currentId == delVideo._id) {
                        window.eventBus.$emit('playing', {
                            video: null,
                            playlist: false,
                            status: false
                        })
                    }

                    // Get Data from Previous Node & Next Node
                    if (delVideo.prev != null) {
                        getVideoById(delVideo.prev, prev => {
                            prevVideo = prev

                            // Update Previous Node
                            updateVideo({
                                _id: prevVideo._id,
                                index: prevVideo.index,
                                prev: prevVideo.prev,
                                videoId: prevVideo.videoId,
                                next: delVideo.next
                            })
                        })
                    }

                    if (delVideo.next != null) {
                        getVideoById(delVideo.next, next => {
                            nextVideo = next

                            // Update Next Node
                            updateVideo({
                                _id: nextVideo._id,
                                index: nextVideo.index,
                                prev: delVideo.prev,
                                videoId: nextVideo.videoId,
                                next: nextVideo.next
                            })
                        })
                    }
                    
                    deleteVideo(video._id, () => {
                        this.fetchAllVideos()
                    })
                })
            },
            deleteAll() {
                deleteAllVideos(() => this.fetchAllVideos())
            },
            dropItem() {
                window.eventBus.$emit('updateList', this.videos)
            },
            fetchAllVideos() {
                const self = this

                getVideos(response => {
                    if (response.length > 0) {
                        const videoIds = response.map((v) => {
                            return v.videoId
                        });

                        SearchById({
                            apiKey: 'AIzaSyB7nVWeBSxb7HC7U3i5SKe6KqXsu8WbcdY',
                            videoId: videoIds.toString()
                        }, result => {
                            self.videos = result

                            self.videos.forEach((v, index) => {
                                v._id = response[index]._id
                                v.index = response[index].index
                            })
                        })
                    } else {
                        self.videos = []
                    }
                })
            },
            handlePlayPrev() {
                const self = this

                self.fetchAllVideos()

                if (self.videos.length > 0) {
                    const currentId = self.$store.getters.getCurrent

                    if (currentId == null) return;

                    getVideoById(currentId, v => {
                        if (v.prev != null) {
                            getVideoById(v.prev, prevVideo => {
                                SearchById({
                                    apiKey: 'AIzaSyB7nVWeBSxb7HC7U3i5SKe6KqXsu8WbcdY',
                                    videoId: prevVideo.videoId
                                }, result => {
                                    result[0]._id = prevVideo._id
                                    result[0].index = prevVideo.index
                                    window.eventBus.$emit('playing', {
                                        video: result[0],
                                        playlist: true,
                                        status: true
                                    })
                                })
                            })
                        }
                    })
                }
            },
            handlePlayNext() {
                const self = this

                self.fetchAllVideos()

                if (self.videos.length > 0) {
                    const currentId = self.$store.getters.getCurrent

                    if (currentId == null) return;

                    getVideoById(currentId, v => {

                        if (v.next != null) {
                            getVideoById(v.next, nextVideo => {
                                SearchById({
                                    apiKey: 'AIzaSyB7nVWeBSxb7HC7U3i5SKe6KqXsu8WbcdY',
                                    videoId: nextVideo.videoId
                                }, result => {
                                    this.$store.dispatch('setCurrent', nextVideo._id)

                                    result[0]._id = nextVideo._id
                                    result[0].index = nextVideo.index
                                    window.eventBus.$emit('playing', {
                                        video: result[0],
                                        playlist: true,
                                        status: true
                                    })
                                })
                            })
                        } else {
                            window.eventBus.$emit('playing', {
                                video: {id: null, _id: null},
                                playlist: false,
                                status: false
                            })
                        }
                    })
                }
            }
        } 
    }
</script>

<style scoped>
    .ghost {
        opacity: 0.5;
        background: #c8ebfb;
    }
</style>