<template>
    <dir class="VideoItem__wrapper">
        <div class="card">
            <div class="card-image">
                <figure class="image is-4by3">
                    <img :src="video.snippet.thumbnails.high.url" alt="Placeholder image">
                </figure>
            </div>

            <div class="card-content">
                <div class="media">
                    <div class="media-left">
                        <figure class="image is-48x48">
                            <img :src="video.snippet.thumbnails.default.url" alt="Placeholder image">
                        </figure>
                    </div>

                    <div class="media-content">
                        <p class="title is-4">{{video.snippet.title}}</p>
                        <p class="subtitle is-6">{{video.snippet.channelTitle}}</p>
                    </div>
                </div>

                <div class="content">
                    {{video.snippet.description}}
                </div>
            </div>

            <footer class="card-footer">
                <a href="#" class="card-footer-item" @click="openVideo">Play</a>
                <a href="#" class="card-footer-item" @click="addToList">Add to List</a>
            </footer>
        </div>
    </dir>
</template>

<script>
    export default {
        props: ['video'],
        methods: {
            openVideo() {
                window.eventBus.$emit('playing', {
                    video: {id: this.video.id.videoId},
                    playlist: false,
                    status: true
                })
            },
            addToList() {
                window.eventBus.$emit('addToList', this.video)
            }
        }
    }
</script>

<style>
    .VideoItem__wrapper {
        padding: 0;
    }
</style>