<template>
    <div class="MediaPlayer__wrapper">
        <youtube
            :video-id="this.video.id"
            :player-vars="{ autoplay: 1 }"
            player-width="100%"
            @ready="ready"
            @ended="ended">
        </youtube>
        <div class="buttons is-centered" v-if="playlist">
            <button class="button is-left" @click="prevVideo">
                <i class="fa fa-lg fa-arrow-left"></i>
            </button>
            <button class="button is-right" @click="nextVideo">
                <i class="fa fa-lg fa-arrow-right"></i>
            </button>
        </div>
    </div>
</template>

<script>
    export default {
        props: ['video', 'playlist'],
        data() {
            return {
                fromPlaylist: false,
                player: null
            }
        },
        
        mounted() {
            window.eventBus.$on('playMedia', this.play);
            window.eventBus.$on('pauseMedia', this.pause);
        },

        methods: {
            prevVideo() {
                window.eventBus.$emit('playPrev')
            },

            nextVideo() {
                window.eventBus.$emit('playNext')
            },

            ready (event) {
                this.player = event.target
                this.$store.dispatch('setCurrent', this.video._id)
            },

            play() {
                this.player.playVideo()
            },

            pause() {
                this.player.pauseVideo()
            },

            ended() {
                if (this.playlist) {
                    window.eventBus.$emit('playNext')
                }
            },
        }
    }
</script>