let axios = require('axios')
let BASE_URL_SEARCH = "https://www.googleapis.com/youtube/v3/search"
let BASE_URL_VIDEOS = "https://www.googleapis.com/youtube/v3/videos"

export const Search = (options, callback) => {
    if (!options.apiKey) throw new Error('Youtube search would require a key.')

    let params = {
        part: 'snippet',
        key: options.apiKey,
        q: options.term,
        maxResults: (options.items) ? options.items : 5,
        type: 'video'
    }

    axios.get(BASE_URL_SEARCH, { params })
        .then(response => {
            if (callback) {
                callback(response.data.items)
            }
        })
        .catch(error => console.error(error))
}

export const SearchById = (options, callback) => {
    if (!options.apiKey) throw new Error('Youtube search would require a key.')

    let params = {
        part: 'snippet',
        key: options.apiKey,
        id: options.videoId
    }

    axios.get(BASE_URL_VIDEOS, { params })
        .then(response => {
            if (callback) {
                callback(response.data.items)
            }
        })
        .catch(error => console.error(error))
}