let axios = require('axios')
let BASE_URL = "https://music-chatbot-api.herokuapp.com/playlist"

export const getVideos = (callback) => {
    axios.get(BASE_URL)
        .then(response => {
            if (callback) {
                callback(response.data)
            }
        })
        .catch(error => console.error(error.message))
}

export const getVideoById = (id, callback) => {
    axios.get(`${BASE_URL}/${id}`)
        .then(response => {
            if (callback) {
                callback(response.data)
            }
        })
        .catch(error => console.error(error.message))
}

export const addVideo = (options, callback) => {
    let params = {
        index: options.index,
        prev: (options.prev) ? options.prev : null,
        videoId: options.videoId,
        next: (options.next) ? options.next : null
    }

    axios.post(BASE_URL, { params })
        .then(response => {
            if (callback) {
                callback(response.data)
            }
        })
        .catch(error => console.error(error.message))
}

export const updateVideo = (options, callback) => {
    let params = {
        index: options.index,
        prev: (options.prev) ? options.prev : null,
        videoId: options.videoId,
        next: (options.next) ? options.next : null
    }

    axios.patch(`${BASE_URL}/${options._id}`, { params })
        .then(response => {
            if (callback) {
                callback(response.data)
            }
        })
        .catch(error => console.error(error.message))
}

export const deleteVideo = (id, callback) => {
    axios.delete(`${BASE_URL}/${id}`)
        .then(response => {
            if (callback) {
                callback(response.data)
            }
        })
        .catch(error => console.error(error.message))
}

export const deleteAllVideos = (callback) => {
    axios.delete(`${BASE_URL}/`)
        .then(response => {
            if (callback) {
                callback(response.data)
            }
        })
        .catch(error => console.error(error.message))
}