<template>
    <article class="media">
        <figure class="media-left">
            <p class="image is-64x64">
                <img :src="video.snippet.thumbnails.default.url">
            </p>
        </figure>
        <div class="media-content">
            <div class="content">
                <p>
                    <strong @click="playVideo" class="video-title">{{ video.snippet.title }}</strong>
                </p>
            </div>
        </div>
        <div class="media-right">
            <button class="delete" @click="deleteVideo"></button>
        </div>
    </article>
</template>

<script>
    export default {
        props: ['video'],
        data() {
            return {
                prevVideo: null,
                nextVideo: null
            }
        },
        methods: {
            deleteVideo() {
                window.eventBus.$emit('deleteVideo', this.video)
            },

            playVideo() {
                const self = this

                window.eventBus.$emit('playing', {
                    video: self.video,
                    playlist: true,
                    status: true
                })
            }
        }
    }
</script>

<style>
    .video-title:hover {
        cursor: pointer;
    }
</style>